body {
	height: 100dvh;
	width: 100dvw;
}

#root {
	height: 100dvh;
	width: 100dvw;
	overflow: hidden;
}

.container {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.modal-outer {
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #000a;
}
.modal-inner {
	padding: 1em;
	background-color: white;
	border-radius: 5px;
}
#level-form {
	display: flex;
	flex-direction: column;
	gap: 1em;
}
.options {
	display: flex;
	width: 40em;
	max-width: 80vw;
}
.options label input {
	display: none;
}
.options label {
	flex: 1;
}
.options label > div {
	background-color: #e6e6e6;
	margin: 0.1em;
	padding: 2em 0;
	text-align: center;
	cursor: pointer;
	border: solid 1px #fff;
}
.options label > div:hover {
	border: solid 1px #aaa;
}
.options input:checked + div {
	background-color: #afa;
}

.modal-outer .footer {
	width: 100%;
}
.modal-outer .footer button {
	width: 100%;
	padding: 0.5em;
	font-size: 1em;
}

.game {
	display: flex;
	border-left: solid 3px #000;
	border-top: solid 3px #000;
	width: 30em;
	height: 30em;
	max-width: 90vw;
	max-height: 90vw;
}
.game.done {
	background-color: #afa;
}
.col {
	display: flex;
	flex: 1;
	flex-direction: column;
}
.cell {
	display: flex;
	align-items: center;
	justify-content: center;
	border-right: solid 1px #000;
	border-bottom: solid 1px #000;
	flex: 1;
	cursor: pointer;
	color: #888;
	font-size: 1.5em;
	font-weight: bold;
}
.cell.b-end {
	border-bottom: solid 3px #000;
}
.cell.r-end {
	border-right: solid 3px #000;
}
.cell.selected {
	background-color: #aff;
}
.cell.original {
	font-weight: bold;
	color: black;
}
.cell.wrong {
	color: red;
}
.cell .ch {
	font-size: 0.8em;
	color: #3317;
}

@media only screen and (max-width: 768px) {
	.cell {
		font-size: 1em;
	}
}

.control {
	display: flex;
	gap: 0.1em;
	margin: 0.2em 0 0 0;
}
.control-panel {
	margin: 2em 0 0 0;
}
.control button.key {
	font-size: 1.5em;
	padding: 0.5em 0.8em;
}
